<template>
    <div v-wechat-title="$route.meta.title" id="box">
        <img id="img" src="../assets/mathplanet-image.png"/>
        <div id="input-box">
            <input id="i-phone" type="text" placeholder="请输入手机号" v-model="phoneNum"/>
            <input id="i-code" type="text" placeholder="请输入验证码" v-model="code"/>
            <button type="button" id="s-send" class="btn btn-info btn-xs" @click="sendCode">{{sendMsg}}</button>
        </div>
        <button id="b-login" class="btn btn-success" type="button" @click="login">登录</button>

        <label id="error-label">{{errorMsg}}</label>

        <div id="myModal" class="modal fade" tabindex="-1" role="dialog">
        <div class="modal-dialog" role="document">
            <div class="modal-content">
            <div class="modal-body">
                <p>您的手机号未注册《迈思星球》</p>
                <p>请先下载《迈思星球》App并注册</p>
            </div>
            <div class="modal-footer">
                <button type="button" class="btn btn-default btn-xs" data-dismiss="modal">取消</button>
                <button type="button" class="btn btn-primary btn-xs">去下载</button>
            </div>
            </div><!-- /.modal-content -->
        </div><!-- /.modal-dialog -->
        </div><!-- /.modal -->
        <span data-toggle="modal" data-target="#myModal" id="s-show-modal"></span>
    </div>
</template>
<script>
export default {
  name: 'Login',
  data(){
      return {
          sendMsg: '发送验证码',
          errorMsg: '',
          timerFinish: true,
          phoneNum: '',
          code: ''
      }
  },
  methods: {
    sendCode(){
        if(this.timerFinish){
            this.timerFinish = false;
            let count = 10;
            this.sendMsg = `请稍后${--count}s`;
            let timer = setInterval(()=>{
                count--;
                this.sendMsg = `请稍后${count}s`;
                if(count <= 0){
                    clearInterval(timer);
                    this.timerFinish = true;
                    this.sendMsg = '发送验证码';
                }
            },1000);
            //请求发送验证码
        }
    },
    login(){
        // this.errorMsg = '暂不可用';
        // $('#s-show-modal').click();
        this.$router.replace('report');
    }
  }
}
</script>
<style>
    #img {
        width: 100%;
        height: 150px;
    }
    #box {
        width: 100%;
    }
    #input-box {
        margin-top: 5%;
        margin-left: 20%;
        margin-right: 20%;
    }
    input::-webkit-input-placeholder {
        text-align: center;
    }
    input[type='text'] {
        border-style: solid;
        border-radius: 18px;
        border-width: 1px;
        border-color: gray;
    }
    #i-phone {
        display: block;
        width: 100%;
        height: 35px;
    }
    #i-code {
        float: left;
        width: 50%;
        height: 25px;
        display: block;
        margin-top: 5%;
    }
    #s-send {
        float: right;
        width: 40%;
        margin-top: 5%;
        font-size: 12px;
        background-color: rgb(12, 153, 235);
    }
    #b-login {
        width: 60%;
        line-height: 30px;
        margin-top: 5%;
    }
    #error-label {
        width: 100%;
        display: block;
        color: red;
        margin-top: 10%;
        font-size: 18px;
    }

</style>



