<template>
    <div v-wechat-title="$route.meta.title" id="box">
        <div id="flex-box">
            <span id="flex-item" v-show="group[0] != null" @click="ongroupClick(0)">{{group[0]}}</span>
            <span id="flex-item" pos="middle" v-show="group[1] != null" @click="ongroupClick(1)">{{group[1]}}</span>
            <span id="flex-item" v-show="group[2] != null" @click="ongroupClick(2)">{{group[2]}}</span>
        </div>
        <div v-show="lifeDays != ''" id="content">
            <p>在迈思星球生活<span style="color:red">{{lifeDays}}</span>天,学习了<span style="color:blue">{{lessonCount}}</span>课程,累计学习<span style="color:green">{{minutes}}</span>分钟</p>
            <p>上周共学习<span style="color:red">{{lastweekLifeDays}}</span>天，学习了<span style="color:red">{{lastweekLessonCount}}</span>节课程,
            累计学习<span style="color:green">{{lastweekMinutes}}</span>分钟，超过全国<span style="color:orange">{{lastweekMorethan}}</span>的小朋友！</p>
            <label>上周学习进度</label>
            <table border="1px" id="tb-lesson" v-html="studyProgress">
            </table>
            <div id="show-detail-box" @click="onDetailClick">
                <span id="s-detail">点击查看答题详情</span>
                <span id="s-detail-icon" v-bind:class="showDetail ? 'glyphicon glyphicon-menu-up' : 'glyphicon glyphicon-menu-down'"></span>
            </div>
            <div id="detail-box" v-show="showDetail">
                <label>上周学习详情</label>
                <table border="1px" id="tb-detail" v-html="studyDetail">
                </table>
            </div>
            <button type='button' class="btn btn-success" id="btn-task" @click="onTaskClick">布置错题任务</button>
        </div>
        <div style="height:50px"></div>
    </div>
</template>

<script>
export default {
    data(){
        return {
            group: [],
            lifeDays: '20',
            lessonCount: '50',
            minutes: '200',
            lastweekLifeDays: '5',
            lastweekLessonCount: '4',
            lastweekMinutes: '100',
            lastweekMorethan: '96%',
            studyProgress: `<tr>
                        <td>日期</td>
                        <td id='lesson-name'>课程名</td>
                        <td>学习时长</td>
                    </tr>`,
            showDetail: false,
            studyDetail: `
                        <tr>
                        <td>日期</td>
                        <td>答题数</td>
                        <td>正确数</td>
                        </tr>   
                        `
        }
    },
    methods: {
        getGroup(){
            this.group.push('张三');
            this.group.push('李四');
            this.group.push('王五');
        },
        ongroupClick(index){
            console.log(index);
        },
        onDetailClick(){
            this.showDetail  = !this.showDetail;
        },
        onTaskClick(){

        },
        getDetail(){
            let arr = [{date:'7月9日', week:'周一', name: '第一课', time:'11分钟', answerSum: '10', rightSub: '10'},{date:'7月10日', week:'周一', name: '第二课第二课第二课第二课第二课第二课第二课第二课第二课第二课第二课第二课', time:'11分钟',answerSum: '10', rightSub: '10'},{date:'7月11日', week:'周三', name: '第三课', time:'11分钟',answerSum: '10', rightSub: '10'}];
            arr.forEach((obj)=>{
                this.studyProgress += `
                            <tr>
                                <td><span>${obj.date}</span><br/><span>${obj.week}</span></td>
                                <td id='lesson-name'>${obj.name}</td>
                                <td>${obj.time}</td>
                            </tr>
                            `;
                this.studyDetail += `
                                    <tr>
                                        <td><span>${obj.date}</span><br/><span>${obj.week}</span></td>
                                        <td id='lesson-name'>${obj.answerSum}</td>
                                        <td>${obj.rightSub}</td>
                                    </tr>
                                    `;
            });
        }
    },
    mounted(){
        this.getGroup();
        this.getDetail();
    }
}
</script>
<style>
    #box {
        width: 100%;
    }
    #flex-box {
        display: -webkit-flex;
        display: flex;
        margin-left: 10%;
        margin-right: 10%;
        margin-top: 5%;
    }
    #flex-item {
        width: 100%;
        height: 50px;
        line-height: 50px;
        background: green;
        color: white;

    }
    #flex-item[pos='middle']{
        margin-left: 10%;
        margin-right: 10%;
    }
    #content {
        margin-left: 10%;
        margin-right: 10%;
        margin-top: 5%;
    }
    #content>p{
        text-align: left;
        font-size: 14px;
    }
    #tb-lesson {
        width: 100%;
        font-size: 12px;
    }
    #lesson-name {
        width: 60%;
    }
    #show-detail-box {
        width: 100%;
        height: 50px;
        margin-top: 5%;
        border: 1px dashed gray;
    }
    #s-detail {
        font-size: 12px;
        color: gray;
        height: 30px;
        line-height: 30px;
        display: block;
    }
    #s-detail-icon {
        display: block;
        font-size: 12px;
        color: gray;
    }
    #detail-box {
        margin-top: 10px;
    }
    #tb-detail {
        width: 100%;
        font-size: 12px;
    }
    #btn-task {
        margin-top: 5%;
        width: 100%;
    }
</style>



